/**
 * Header cart
 */
.site-header-cart {
	position: relative;
	margin: 0;
	padding: 0;
	@include clearfix;

	.cart-contents {
		text-decoration: none;
	}

	.widget_shopping_cart {
		display: none;
	}

	.product_list_widget {
		margin: 0;
		padding: 0;
	}
}

/**
 * Star rating
 */
.star-rating {
	overflow: hidden;
	position: relative;
	height: 1.618em;
	line-height: 1.618;
	font-size: 1em;
	width: 5.3em;
	font-family: 'star';
	font-weight: 400;

	&:before {
		content: "\53\53\53\53\53";
		opacity: .25;
		float: left;
		top: 0;
		left: 0;
		position: absolute;
	}

	span {
		overflow: hidden;
		float: left;
		top: 0;
		left: 0;
		position: absolute;
		padding-top: 1.5em;
	}

	span:before {
		content: "\53\53\53\53\53";
		top: 0;
		position: absolute;
		left: 0;
		color: $color__link;
	}
}

p.stars {
	a {
		position: relative;
		height: 1em;
		width: 1em;
		text-indent: -999em;
		display: inline-block;
		text-decoration: none;
		margin-right: 1px;
		font-weight: 400;

		&:before {
			display: block;
			position: absolute;
			top: 0;
			left: 0;
			width: 1em;
			height: 1em;
			line-height: 1;
			font-family: "star";
			content: "\53";
			color: $color__text-main;
			text-indent: 0;
			opacity: .25;
		}

		&:hover {
			~ a:before {
				content: "\53";
				color: $color__text-main;
				opacity: .25;
			}
		}
	}

	&:hover {
		a {
			&:before {
				content: "\53";
				color: $color__link;
				opacity: 1;
			}
		}
	}

	&.selected {
		a.active {
			&:before {
				content: "\53";
				color: $color__link;
				opacity: 1;
			}

			~ a:before {
				content: "\53";
				color: $color__text-main;
				opacity: .25;
			}
		}

		a:not(.active) {
			&:before {
				content: "\53";
				color: $color__link;
				opacity: 1;
			}
		}
	}
}

/**
 * Tabs
 */
.woocommerce-tabs {
	ul.tabs {
		list-style: none;
		margin: 0;
		padding: 0;
		text-align: left;

		li {
			display: block;
			margin: 0;
			position: relative;

			a {
				padding: 1em 0;
				display: block;
			}
		}
	}

	.panel {
		h2:first-of-type {
			margin-bottom: 1em;
		}
	}
}

/**
 * Password strength meter
 */
.woocommerce-password-strength {
	text-align: right;

	&.strong {
		color: $woocommerce__color-success;
	}

	&.short {
		color: $woocommerce__color-error;
	}

	&.bad {
		color: $woocommerce__color-error;
	}

	&.good {
		color: $woocommerce__color-info;
	}
}

/**
 * Forms
 */
.woocommerce-form-login {
	@include prefix( box-shadow, 0 1px 6px 0 rgba( #000, .4 ), o moz webkit );
	width: 500px;
	margin: 15px auto;
	padding: 20px 24px;
	justify-content: center;
	border-radius: 10px;

	p:nth-child(3) {
		display: flex;
		align-items: center;
		justify-content: center;

		label {
			margin-left: 15px;
		}
	}

	p:last-child {
		margin-bottom: 0;
	}

	label {
		margin-bottom: 0;
	}
}

.form-row {
	&.woocommerce-validated {
		input.input-text {
			box-shadow: inset 2px 0 0 $woocommerce__color-success;
		}
	}

	&.woocommerce-invalid {
		input.input-text {
			box-shadow: inset 2px 0 0 $woocommerce__color-error;
		}
	}
}

.required {
	color: red;
}

/**
 * Notices
 */
.woocommerce-message,
.woocommerce-info,
.woocommerce-error,
.woocommerce-noreviews,
p.no-comments {
	@include clearfix;
	background-color: $woocommerce__color-success;
	clear: both;
}

.woocommerce-info,
.woocommerce-noreviews,
p.no-comments {
	background-color: $woocommerce__color-info;
}

.woocommerce-error {
	background-color: $woocommerce__color-error;
}

.demo_store {
	position: fixed;
	left: 0;
	bottom: 0;
	right: 0;
	margin: 0;
	padding: 1em;
	background-color: $woocommerce__color-info;
	z-index: 9999;
}

/**
 * Store
 */
.woocommerce {
	
	.product-categories {
		list-style: none;

		li {

			a {
				color: #222222;

				&:hover {
					color: $color__link-hover;
					text-decoration: none;
				}
			}
		}
	}

	.add_to_cart_button, .single_add_to_cart_button {
		@include prefix( box-shadow, 0 1px 6px 0 rgba( #000, .4 ), o moz webkit );
		@include prefix( transition, all .2s ease-in-out, o moz webkit );
		background: $color__secondary;
		color: #fff;
		padding: 8px 15px;
		text-align: center;
		border-radius: 10px;
		font-family: $font__opsanlight;
		margin: 15px auto;

		&:hover, &:focus, &:active {
			background: $color__primary;
			color: $color__link-hover;
		}
	}
}
/**
 * End Store
 */

/**
 * Account
 */
.woocommerce {
	
	.woocommerce-MyAccount-navigation {
		float: none;
		@include prefix( transition, all .2s ease-in-out, o moz webkit );
		width: 100%;

		ul {
			list-style: none;
			li {
				text-align: left;

				&:not( :first-child ) {
					margin-top: 8px;
				}

				a {
					color: #222222;

					&:hover {
						color: $color__link-hover;
						text-decoration: none;
					}
				}
			}
		}
	}

	.woocommerce-MyAccount-content {
		float: none;
		@include prefix( transition, all .2s ease-in-out, o moz webkit );
		width: 100%;

		p {
			text-align: left !important;
		}
	}

	a {
		color: #222222;

		&:hover {
			color: $color__link-hover;
			text-decoration: none;
		}
	}

	@include media-breakpoint-up( sm ) {

		.woocommerce-MyAccount-navigation {
			float: left;
			width: 29.8%;
		}

		.woocommerce-MyAccount-content {
			float: left;
			width: 49.8%;
		}
	}
}
/**
 * End Account
 */

@media screen and (min-width: 48em) {
	/**
	 * Header cart
	 */
	.site-header-cart {
		.widget_shopping_cart {
			position: absolute;
			top: 100%;
			width: 100%;
			z-index: 999999;
			left: -999em;
			display: block;
			box-shadow: 0 3px 3px rgba(0, 0, 0, 0.2);
		}

		&:hover,
		&.focus {
			.widget_shopping_cart {
				left: 0;
				display: block;
			}
		}
	}
}
